<template>
    <!-- Popular block starts -->
        <section class="popular">
            <b-container>
                <div class="title text-sm-center m-b-30">
                    <h2>Popular este mês em sua cidade</h2>
                    <p class="lead">A maneira mais fácil para sua comida favorita</p>
                </div>                    
                <b-row>
                <div class="col-xs-12 col-sm-6 col-md-12 food-item">         
                    <carousel :navigationEnabled="true" :loop="true" :autoplay="true" :perPageCustom="[[320,1], [375, 1], [768, 3], [1024, 4]]">
                        <slide v-for="(restaurant) in popularRestaurants" :key="restaurant.id">
                            <div class="col-xs-12 col-sm-6 col-md-12 food-item">
                                <div class="food-item-wrap">
                                    <div class="figure-wrap">
                                    <b-img rounded :src="restaurant.img" alt="Restaurant logo" style="width: 100%" height="210"/>
                                    </div>
                                    <div class="content">
                                        <h4>
                                            <a href="profile.html">{{restaurant.name}}</a>
                                        </h4>
                                        <div>{{restaurant.description}}</div>
                                        <div class="rating">
                                            <i class="fa fa-star"></i>
                                            <i class="fa fa-star"></i>
                                            <i class="fa fa-star"></i>
                                            <i class="fa fa-star"></i>
                                            <i class="fa fa-star-o"></i>
                                        </div>
                                        <div>
                                            <a href="#">198 reviews</a>  
                                        </div>
                                        <div class="distance">
                                            <i class="fa fa-map-marker mr-1"></i>{{restaurant.distance.toFixed(1)}} km
                                        </div>
                                    </div>
                                    <div class="restaurant-block">
                                        <div class="left">
                                            <b-link class="pull-left" to="profile">
                                                <b-img rounded :src="restaurant.img" alt="Restaurant logo" width="50" height="46" />
                                            </b-link>
                                            <div class="pull-left right-text">
                                                <a href="#">{{restaurant.name}}</a>
                                                <span>{{restaurant.street}}</span>
                                            </div>
                                        </div>
                                        <div class="right-like-part pull-right">
                                            <i class="fa fa-heart-o"></i>
                                            <span>48</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </slide>
                    </carousel>
                </div>
                </b-row>
                          
            </b-container>
        </section>
        <!-- Popular block ends -->
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      popularRestaurants: []
    }
  },
  computed: {
    ...mapGetters({
      lastUsed: 'lastUsed'
    })
  },
  methods: {
    async getPopularRestaurants () {
      let response = []
      try {
        let _city = this.lastUsed.city ? this.lastUsed.city : 'Uberlândia'
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        response = await _axios.get(
          '/restaurant/city/' + _city + '/latitude/-18.8960045/longitude/-48.2678202/limit/10'
        )
      } catch (e) {
        console.log(e)
      }
      return response
    }
  },
  created () {
    this.getPopularRestaurants().then((response) => {
      return response.data
    }).then(response => {
      if (response && response.data) {
        return response.data
      }
      return []
    }).then((response) => {
      this.popularRestaurants = response
    })
  }
}
</script>

<style scoped>
/* ==========================
*  Popular block
============================*/

.popular {
    padding: 35px 0 30px;
    background-size: 100%;
}

.food-item-wrap {
    border: 1px solid #eaebeb;
    border-radius: 2px;
    overflow: hidden;
    margin-bottom: 30px;
    background: #fafaf8;
}

.food-item-wrap h5 a {
    color: #25282b;
    font-size: 21px;
    font-weight: 600;
}

.food-item-wrap .title h3 {
    font-size: 16px;
    margin-bottom: 35px;
    color: #414551;
    line-height: 21px;
    font-weight: 300;
}

.food-item-wrap .price {
    font-size: 21px;
    font-weight: 700;
    color: #000;
    margin-top: 4px;
    display: inline-block;
}

.food-item-wrap .product-name {
    margin-bottom: 20px;
}

.food-item-wrap .content {
    padding: 25px 15px 20px;
}

.food-item-wrap .restaurant-block {
    border-top: 1px solid #eaebeb;
    float: left;
    width: 100%;
}

.food-item-wrap .right-text {
    margin-left: 10px;
}

.food-item-wrap .right-text a {
    display: block;
}

.food-item-wrap .left {
    float: left;
    padding: 8px 15px;
}

.food-item-wrap .left img {
    padding-top: 3px;
}

@media (min-width: 320px) and (max-width: 568px) {
    .food-item-wrap .left {
        float: left;
        padding: 10px;
    }
    .food-item .left img {
        width: 45px
    }
    .food-item-wrap .right-text {
        font-size: 13px;
    }
}

.food-item-wrap .right {
    padding: 10px 0;
}

.food-item-wrap .right-like-part {
    border-left: 1px solid #eaebeb;
    padding: 25px 15px;
    font-size: 13px;
}

@media (min-width: 280px) and (max-width: 580px) {
    .food-item-wrap .right-like-part {
        padding: 24px 10px;
        font-size: 10px;
    }
}

.food-item-wrap .right-like-part img {
    vertical-align: middle;
    margin-right: 5px;
}

.food-item-wrap .right-text a {
    color: #25282b;
    font-weight: 400;
}

.food-item-wrap .right-text span,
.food-item-wrap .right-like-part,
.food-item-wrap .right-like-part span {
    font-weight: 300;
    color: #748796;
}

.food-item-wrap .figure-wrap {
    position: relative;
    height: 210px;
}

.food-item-wrap .food-item-wrap figure {
    position: relative;
    overflow: hidden;
    margin-bottom: 15px;
}

.food-item-wrap .food-item-wrap figure img {
    width: 100%;
}

.food-item-wrap .figure-wrap:after {
    position: absolute;
    bottom: -1px;
    left: 0;
    content: "";
    background: url(../../../assets/img/home/zig-zag.png);
    width: 100%;
    height: 5px;
}

.food-item-wrap:hover h5>a,
.food-item-wrap:hover .right-text>a {
    color: #ffb42d;
}

.food-item-wrap .figure-text {
    position: absolute;
    top: 0;
    padding: 13px 28px 18px 18px;
    width: 100%;
    height: 100%;
}

.food-item-wrap .figure-text .bottom {
    position: absolute;
    bottom: 25px;
    float: left;
    width: 87%;
}

.food-item-wrap .review {
    margin-top: 5px;
    font-size: 10px;
    text-transform: uppercase;
}

.food-item-wrap .distance {
    background: #ffb42d;
    border-radius: 3px;
    color: black;
    font-size: 14px;
    padding: 0 10px;
    display: inline-block;
}

.food-item-wrap .rating i {
    color: #ffd953;
    font-size: 16px;
}

.food-item-wrap .rating .fa-star-o {
    font-size: 17px;
}

.food-item-wrap .review {
    right: 30px;
    position: absolute;
    bottom: 30px;
}

.food-item-wrap .review a {
    color: #fff;
}

.food-item-wrap .left-sidebar,
.food-item-wrap .right-sidebar {
    width: 300px;
}

.food-item-wrap .lf-ghost {
    width: 395px!important;
    float: left;
}

@media (min-width: 544px) and (max-width: 767px) {
    .food-item-wrap .right-text {
        margin: 5px 10px;
        font-size: 14px;
        line-height: 20px;
    }
    .food-item-wrap .right-like-part {
        width: 100%;
        padding: 5px 10px;
        background: #fff;
        border: 0;
        border-top: 1px solid #eaebeb;
        text-align: center;
    }
}

@media (min-width: 768px) and (max-width: 991px) {
    .food-item-wrap .restaurant-block {
        text-align: center;
    }
    .food-item-wrap .left {
        float: none
    }
    .food-item-wrap .pull-left {
        float: none
    }
    .food-item-wrap .right-text span,
    .food-item-wrap .right-like-part,
    .food-item-wrap .right-like-part span {
        width: 100%;
    }
    .right-like-part.pull-right {
        background-color: #fff;
        border: 0;
        border-top: 1px solid #eaebeb;
        padding: 10px;
    }
    .food-item-wrap .price-btn-block {
        text-align: center
    }
    .food-item-wrap .price-btn-block .price {
        margin-bottom: 15px;
        display: block;
    }
    .food-item-wrap .price-btn-block .btn {
        float: none;
    }
}

@media (min-width: 992px) and (max-width: 1199px) {
    .food-item-wrap .left {
        padding: 10px;
        font-size: 11px;
        line-height: 20px;
    }
}
</style>


